import React, { PropTypes }  from 'react';
import {Form, Button, Icon,Input,Col} from 'antd';
import PictureWall from '../upload/PictureWall';
import {downdomain, ueditorconfig, ueditorall} from '../../utils/config';



const FormItem = Form.Item;

class EditProfile extends React.Component {
  handleSubmit = (e) => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      if (!err&&values.newPassword==values.newPasswords) {
        let params = {
          email: values.email,
          name:values.name,
          headUrl: values.headUrl,
          newPassword:values.newPassword,
          oldPassword:values.oldPassword,
          newPasswords:values.newPasswords
        }
        this.props.onSubmit(params)
      }else{
         this.props.onFaulttip()
      }
    });
  }

  onListChange = (fileList) => {
    this.fileList = fileList;
    const {getFieldsValue, setFieldsValue, validateFields} = this.props.form;
    const fieldsValue = getFieldsValue();
    const headUrl = fileList.map((item)=>item.response.key);
    const newFieldsValue = {...fieldsValue, 'headUrl': headUrl.length > 0 ? downdomain + headUrl : ''};
    setFieldsValue(newFieldsValue);
    validateFields();
  }

  render() {
    const { getFieldDecorator } = this.props.form;
    const {address, email,name,usernameVisible,editVisible,useremailVisible,headUrl} = this.props;
    const fileList = headUrl ? [{
      uid: -1,
      status: 'done',
      name: headUrl,
      url: headUrl
    }] : [];
    const formItemLayout = {
      labelCol: { span: 6 },
      wrapperCol: { span: 6 },
    };
    return (
        <Form onSubmit={this.handleSubmit}>
          {usernameVisible?
            <div>
          <FormItem
            {...formItemLayout}
            label="用户名"
          >
            {getFieldDecorator('name', {
              initialValue: name,
              rules: [
                { required: true, message: '用户名不能为空' },
              ],
            })(<Input/>
            )}
          </FormItem>
          <FormItem
          {...formItemLayout}
            label="上传头像"
            >
          {getFieldDecorator('headUrl', {
            initialValue: headUrl,
              rules: [
          { required: true, message: '封面不能为空' },
            ],
          })(<Input type="hidden"/>)}
            <PictureWall maxFile={1} fileList={fileList} onListChange={this.onListChange}/>
            </FormItem>
            </div>:''}
          {useremailVisible?
          <FormItem
            {...formItemLayout}
            label="绑定邮箱"
          >
            {getFieldDecorator('email', {
              initialValue: email,
              rules: [
                { required: true, message: '邮箱不能为空' },
              ],
            })(<Input type="email"/>
            )}
          </FormItem>:''}
          {editVisible?
          <FormItem
            {...formItemLayout}
            label="请输入旧密码"
          >
            {getFieldDecorator('oldPassword', {
              rules: [
                { required: true, message: '密码不能为空' },
              ],
            })(<Input type="password"/>
            )}
          </FormItem>:''}
          {editVisible?
            <FormItem
              {...formItemLayout}
              label="请输入新密码"
            >
              {getFieldDecorator('newPassword', {
                rules: [
                  { required: true, message: '密码不能为空' },
                ],
              })(<Input type="password"/>
              )}
            </FormItem>:''}
          {editVisible?
            <FormItem
              {...formItemLayout}
              label="请再次输入密码"
            >
              {getFieldDecorator('newPasswords', {
                rules: [
                  { required: true, message: '密码不能为空' },
                ],
              })(<Input type="password"/>
              )}
            </FormItem>:''}
          <FormItem wrapperCol={{ span: 12, offset: 6 }}>
            <Col span={6} style={{ textAlign: 'right' }}>
              <Button type="primary" htmlType="submit">保存</Button>
            </Col>
          </FormItem>
        </Form>
    );
  }
}
export default Form.create()(EditProfile);



