import React from 'react';
import { connect } from 'dva';
import {Button,Modal,InputNumber} from 'antd';
import styles from './UserDetails.less';
import UserProfile from '../components/userDetails/userProfile'
import EditProfile from '../components/userDetails/editProfile'
import CustomerOrder from '../components/userDetails/customerOrder'
import Finances from '../components/userDetails/MyFinances'
function UserDetails({userDetails,dispatch,customerOrder,MyFinances}) {
  const {editVisible, modalVisible,baseinforVisible,payforVisible,coutmonVisible,runorderVisible,name,headUrl,usernameVisible,useremailVisible,id,mobile,remainAmount,changefalVisible} = userDetails;
  const {myorderList,pagination,loading}=customerOrder;
  const {MyFinancesList,Pagination,Loading}=MyFinances;
  const userProfile = {
    ...userDetails,
    onEditprofile () {
      dispatch({
        type: 'userDetails/editProfile',
      })
    },
    onEditusername(){
      dispatch({
        type: 'userDetails/usernameplay',
      })
    },
    onEditemail(){
      dispatch({
        type: 'userDetails/useremailplay',
      })
    }
  }
  const editProfile = {
    ...userDetails,
    onSubmit:(values) => {
       let {oldPassword,newPassword} = values;
      dispatch({
        type:editVisible? 'userDetails/updater':'userDetails/update',
        payload: editVisible?{oldPassword,newPassword,id}:values
      })
    },
    onFaulttip:()=>{
      dispatch({
        type: 'userDetails/editFault',
      })
    }
  }
  const MyorderListProps = {
    myorderList: myorderList,
    loading,
    pagination: pagination,
  }
  const MyFinancesListProps = {
    MyFinancesList: MyFinancesList,
    Loading,
    Pagination: Pagination,
  }
  function onPayBtn() {
    let title = '客户充值';
    dispatch({
      type: 'bill/bcPayBtn',
      payload: {
        title: title,
        amount: 100,
        optional: {
          orderId: 'XXX'
        }
      }
    })
  }
  const showpayfor=()=> {
    dispatch({
      type: 'userDetails/showpayfor',
    })
  }
  const showRunorder=()=> {
    dispatch({
      type: 'userDetails/showrunorder',
    })
  }
  const showBaseinfor=()=> {
    dispatch({
      type: 'userDetails/showbaseinfor',
    })
  }
  const showCountmoney=()=>{
    dispatch({
      type: 'userDetails/showcountmoney',
    })
  }
  const showModal = ()=>{
    dispatch({
      type: 'userDetails/showDeposit'
    })
  }
  const hideModal = () =>{
    dispatch({
      type: 'userDetails/hideDeposit'
    })
  }
  const ModalGen =() => (
    <Modal title="充值" visible={modalVisible}  onCancel={hideModal}>
      <InputNumber placeholder="请输入充值金额"/>
      <Button onClick={onPayBtn}>充值</Button>
    </Modal>
  )
  return (
    <div className={styles.user_detail}>
      <div className={styles.use_picborder}>
        <img src={headUrl} alt="" className={styles.user_detail_picture}/>
      </div>
      <div className={styles.user_profile_top}>
        <p className={styles.user_profile_name}>{name?name:mobile}</p>
        <p className={styles.saying}>纵有疾风起,人生不言弃</p>
        <span className={styles.remaincount}>账户余额:&nbsp;&#165;{remainAmount}</span>
        <a onClick={showModal} className={styles.user_paybut}>充值</a>
      </div>
      <div className={styles.clearfix}></div>
      <div className={styles.user_myaccount}>
        <div className={styles.user_myaccount_theme}>
          <img src="http://oonavif60.bkt.clouddn.com/default/profile/userheaduserhead.png" alt=""/>
          我的账户
        </div>
        <p onClick={showBaseinfor} className={baseinforVisible?styles.leftborder_show:''}>基本信息</p>
        <p onClick={showRunorder} className={runorderVisible||payforVisible?styles.leftborder_show:''}>我的订单</p>
        <p onClick={showCountmoney} className={coutmonVisible?styles.leftborder_show:''}>账户财务</p>
      </div>
      <div className={styles.mycountright}>
        {editVisible?
          <div>
            <p className={styles.user_count_theme}>基本信息修改</p>
            <EditProfile {...editProfile}/>
          </div>
          :''
        }
        {usernameVisible?
          <div>
            <p className={styles.user_count_theme}>基本信息修改</p>
            <EditProfile {...editProfile}/>
          </div>
          :''
        }
        {useremailVisible?
          <div>
            <p className={styles.user_count_theme}>基本信息修改</p>
            <EditProfile {...editProfile}/>
          </div>
          :''
        }
        {baseinforVisible?
          <div>
            <p className={styles.user_count_theme}>基本信息</p>
            <UserProfile {...userProfile}/>
          </div>
          :''
        }
        {changefalVisible?
          <p className={styles.fault_tip}>输入错误,请重新输入!</p>
           :''
        }
        {runorderVisible?
          <div>
            <p className={styles.user_count_theme}><a onClick={showRunorder} className={runorderVisible?styles.runing:''}>进行中的订单</a><a onClick={showpayfor} className={styles.outing}>过往订单</a></p>
            <CustomerOrder {...MyorderListProps}/>
            <a href="MyOrder" style={{marginLeft:'50%'}}>更多</a>
          </div>
          :''
        }
        {payforVisible? <div>
          <p className={styles.user_count_theme}><a  onClick={showRunorder} >进行中的订单</a><a onClick={showpayfor} className={payforVisible?styles.runing:''}>过往订单</a></p>
          过往订单
        </div>:''}
        {coutmonVisible?<div>
          <p className={styles.user_count_theme}>账户财务</p>
          <Finances {...MyFinancesListProps}/>
          <a href="MyFinances" style={{marginLeft:'50%'}}>更多</a>
        </div>:''}
      </div>
      <div className={styles.clearfix}></div>
      <ModalGen/>
    </div>
  );
}

function mapStateToProps({userDetails,customerOrder,MyFinances}) {
  return {userDetails,customerOrder,MyFinances};
}

export default connect(mapStateToProps)(UserDetails);



